import config from '../../components/config/config.vue'
export default{
	data(){
		return{
			userstatus:"0", //身份切换 0发货人  1运送商
			grade:0,
			
			avatarUrl:'../../static/nickname.png',//头像
			order:"Nil",				
			nickname:'Nil',
			
			watch:0,
			
			//变化ui栏
			photo1:"",
			txt1:"",
			photo2:"",
			txt2:"",
			photo3:"",
			txt3:"",
			photo4:"",
			txt4:"",
			
			
			token:'',
			loginState:'',
		}
	},
	onLoad() {
		
	},
	onShow() {				
		uni.startPullDownRefresh();				
	},
	onTabItemTap(){
		uni.startPullDownRefresh();
	},
	onPullDownRefresh() {
		this.token = uni.getStorageSync('token');
		var tmp= uni.getStorageSync('userstatus');
		if(tmp){
			this.userstatus=tmp
		}
		this.onloadui()
		this.onloadmyself()		
		this.onloadwatch()
		this.getgrade()



	    setTimeout(function () {
	        uni.stopPullDownRefresh();
	    }, 1000);
	},
	methods:{
		onloadmyself(){
			if(this.token){
			  	uni.request({
			  	    url: config.httpUrl +'/api/v1/user/me',
			  		method:"GET",
			  	    header: {
			  	       'content-type': 'application/x-www-form-urlencoded',
			  		   'Authorization' : this.token,
			  	    },
			  	    success: (res) => {
			  			var jsondata=res.data
			  			if(jsondata.code=='0'){
			  				this.nickname=jsondata.data.nickname
							if(jsondata.data.avatar){
								this.avatarUrl=jsondata.data.avatar
							}
			  			}else{
			  				config.txtshow(jsondata.msg)				  				
			  			}								
			  	    },
			  		fail: () =>{  
			  			config.txtshow('网络异常')
			  	     }, 
			  	});
			  }else{
			  	//跳转到登录界面
			  	uni.redirectTo({
			      url: '../user/login'
			  });
			    
			}
		},
		onloadui(){
			if(this.userstatus=="1"){
				this.order="运货商",								
				this.photo1="../../static/order.png",
				this.txt1="发布",
				this.photo2="../../static/order.png",
				this.txt2="已发布",
				this.photo3="../../static/time.png",
				this.txt3="处理中",
				this.photo4="../../static/benefits.png",
				this.txt4="已完成"
			}else{
				this.order="寄货人",
				this.photo1="../../static/time.png",
				this.txt1="运送中",
				this.photo2="../../static/benefits.png",
				this.txt2="已完成",
				this.photo3="../../static/star.png",
				this.txt3="收藏夹",
				this.photo4="../../static/view.png",
				this.txt4="足迹"
			}
		},
		
		//关注
		onloadwatch(){
			if(this.token){
			  	uni.request({
			  	    url: config.httpUrl +'/api/v1/user/friendlist',
			  		method:"GET",
			  	    header: {
			  		   'Authorization' : this.token,
			  	    },
			  	    success: (res) => {
			  			var jsondata=res.data
			  			if(jsondata.code=='0'){
			  				this.watch=jsondata.data.length
			  			}else{
			  				config.txtshow(jsondata.msg)				  				
			  			}								
			  	    },
			  		fail: () =>{  
			  			config.txtshow('网络异常')
			  	     }, 
			  	});
			  }else{
			  	//跳转到登录界面
			  	uni.redirectTo({
			      url: '../user/login'
			  });
			  }
		},
		
		//跳转关注
		gowatch(){
			uni.navigateTo({
				url: '../user/watch'	
			})
		},
		//信息管理
		gouserspace(){
			uni.navigateTo({
				url: '../user/userspace'	
			})
		},
		//switchid
		switchid(){
			var that=this
			uni.showModal({
			    title: '提示',
			    content: '确定要切换身份么？',
			    success: function (res) {
			        if (res.confirm) {
			           if(that.userstatus=="1"){
							that.userstatus="0"
							uni.removeStorageSync('userstatus');
							uni.setStorageSync('userstatus',"0")
						}else if(that.userstatus=="0"){
							that.userstatus="1"
							uni.removeStorageSync('userstatus');
							uni.setStorageSync('userstatus',"1")
						}else{
							//非法操作
						}
						uni.startPullDownRefresh();		
					   
			        } else if (res.cancel) {
			            //console.log('用户点击取消');
			        }
			    }
			});
			
			
		},
		
		//运送中0/发布1
		order1click(){
			if(this.userstatus=="1"){
				uni.navigateTo({
					url:"../commodity/add"
				})
			}else if(this.userstatus=="0"){
				uni.navigateTo({
					url:"../order/user_dealing"
				})
			}else{
				//非法操作
			}
		},
		//已完成0/已发布1
		order2click(){
			if(this.userstatus=="1"){
				uni.navigateTo({
					url:"../commodity/mypublished"
				})
			}else if(this.userstatus=="0"){
				uni.navigateTo({
					url:"../order/user_finish"
				})
			}else{
				//非法操作
			}
		},
		//收藏夹0/处理中1
		order3click(){
			if(this.userstatus=="1"){
				uni.navigateTo({
					url:"../order/businesser_dealing"
				})
			}else if(this.userstatus=="0"){
				uni.navigateTo({
					url:"../commodity/collect"
				})
			}else{
				//非法操作
			}
		},
		//足迹0/已完成1
		order4click(){
			if(this.userstatus=="1"){
				uni.navigateTo({
					url:"../order/businesser_finish"
				})
			}else if(this.userstatus=="0"){
				uni.navigateTo({
					url:"../commodity/watchhistory"
				})
			}else{
				//非法操作
			}
		},
		getgrade(){
			uni.request({
			    url: config.httpUrl +'/api/v1/user/grade',
				method:"GET",
			    header: {
				   'Authorization' : this.token,
			    },
				data:{
					'num':this.userstatus
				},
			    success: (res) => {
					var jsondata=res.data
					if(jsondata.code=='0'){
						this.grade=jsondata.data
					}else{
						config.txtshow(jsondata.msg)				  				
					}								
			    },
				fail: () =>{  
					config.txtshow('网络异常')
			     }, 
			});
		}
		
		
	}
}
